<template>
  <div id="customized-edit">
    <div class="edit-main">
      <canvas id='edit_canvas' width='100%' height='100%'></canvas>
    </div>
    <div class="edit_bar">
      <div class="edit-btn edit-btn1"></div>
      <div class="edit-btn edit-btn2"></div>
      <div class="edit-btn edit-btn3">
        <input type="file" accept="image/*" @change="uploadImg($event)" >
      </div>
      <div class="edit-btn edit-btn4"></div>
      <div class="edit-btn edit-btn5"></div>

    </div>
  </div>
</template>

<script>
  //  import AlloyFinger from 'alloyfinger'
 // import AlloyPaper from 'alloyfinger/asset/alloy_paper'


//  import AlloyFingerPlugin from 'alloyfinger/vue/alloy_finger.vue'

 /* Vue.use(AlloyFingerPlugin,{
    AlloyFinger
  })*/
//  import AlloyFinger from 'alloyfinger'

  export default {
    data () {
      return {
        Stage: ''
      }
    },
    watch: {},
    mounted(){
      //console.log(this.getData());
      var stage = new AlloyPaper.Stage("#edit_canvas");
      console.log(AlloyFinger)
      console.log(stage)
    },
    methods: {
      uploadImg($event){
        var file = $event.target.files;
        //console.log(file[0])
        var URL = function(blob){
          return window[window.webkitURL ? 'webkitURL' : 'URL']['createObjectURL'](blob);
        };
        var blob = URL(file[0]);
        console.log(blob)
      },
      addImg(src){
        var ld = new AlloyPaper.Loader();
        ld.loadRes([
          { id: "simg", src: src }
        ]);
        ld.complete(function () {
          var bmp = new AlloyPaper.Bitmap(ld.get("simg"));
          // console.log(bmp);
          bmp.originX = 0.5;
          bmp.originY = 0.5;
          bmp.x = stage.width / 2;
          bmp.y = stage.height / 2;
          bmp.scaleX =  bmp.scaleY = stage.width/bmp.width;

          stage.add(bmp);

          $(".addImgFrame").hide();
          stage.update();

          var initScale = 1;
          new AlloyFinger(bmp, {
            multipointStart: function () {
              initScale = bmp.scaleX;
            },
            rotate: function (evt) {
              bmp.rotation += evt.angle;
              stage.update();
            },
            pinch: function (evt) {
              bmp.scaleX = bmp.scaleY = initScale * evt.scale;
              stage.update();
            },
            pressMove: function (evt) {
              if(isCP){
                bmp.x += evt.deltaX*5;
                bmp.y += evt.deltaY*5;
              }else{
                bmp.x += evt.deltaX*3;
                bmp.y += evt.deltaY*3;
              }

              stage.update();
            },
            singleTap: function(evt){
              if(bmp.id != 1){
                delIcon(bmp);
              }
            }

          });
        });
      }

    }
  }
</script>

<style lang="scss">
  @import "../../sass/baseSass.scss";
  #customized-edit{
    height: 100%;
    width: 100%;
    background: #d5fbfd;

    .edit-main{
      height: 100%;
      width: 100%;
    }
    .edit_bar{
      width: 100%;
      height: box(100);
      background: rgba(0,0,0,0.5);
      position: fixed;
      bottom: 0;
      left: 0;

      .edit-btn{
        width: 20%;
        height: 100%;
        position: relative;
        float: left;
      }
      .edit-btn1{
        background: url("../../assets/edit_uploadFile.png") no-repeat center center;
        background-size: auto 75%;
      }
      .edit-btn2{
        background: url("../../assets/edit_param.png") no-repeat center center;
        background-size: auto 75%;
      }
      .edit-btn3{
        background: url("../../assets/edit_img.png") no-repeat center center;
        background-size: auto 75%;
        input{
          width: 100%;
          height: 100%;
          filter:alpha(opacity=0);
          -moz-opacity:0;
          -khtml-opacity:0;
          opacity: 0;
        }
      }
      .edit-btn4{
        background: url("../../assets/edit_type.png") no-repeat center center;
        background-size: auto 75%;
      }
      .edit-btn5{
        background: url("../../assets/edit_dir.png") no-repeat center center;
        background-size: auto 75%;
      }
    }
  }
  #edit_canvas{
    width: 100%;
    height: 100%;
  }

</style>



